.acti-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    margin: 0;
    padding: 0;
}

.acti-card {
    max-width: 20em;
    padding: 0.5em;
}

.acti-card * {
    padding: 0.25em;
    margin: 0;
}

.acti-card p {
    font-size: 0.9em; 
    text-align: justify;
}